/*

Funnel Viewport

Has 2 panels
- Sidebar (left)
- Mainbar (right)

Responsive behavior
Sidebar hides and can be opened by a toggle that is in view. The sidebar becomes a "sort of" overlay.
Also the sidebar get's it's own scroll context

+-------+--------------------------+
|       |                          |
|       |                          |
|       |                          |
|       |                          |
|       |                          |
|       |                          |
|       |                          |
+-------+--------------------------+

*/
.c-funnel-viewport {
  position: relative;
  /* states */ }
  .c-funnel-viewport__row {
    overflow: auto; }
    .c-funnel-viewport__row:after {
      content: '';
      position: fixed;
      background-color: rgba(0, 0, 0, 0.3);
      height: 100%;
      width: 100%;
      left: 0;
      top: 0;
      z-index: 1;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.2s ease-out; }
    @media (min-width: 768px) {
      .c-funnel-viewport__row {
        display: flex;
        flex-wrap: nowrap;
        align-items: stretch; } }
  .c-funnel-viewport__sidepanel {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 80%;
    background-color: white;
    transform: translateX(-100%);
    z-index: 200; }
    @media (min-width: 768px) {
      .c-funnel-viewport__sidepanel {
        position: static;
        top: auto;
        left: auto;
        width: 500px;
        transform: translateX(0);
        flex-grow: 0; } }
    .c-funnel-viewport__sidepanel__container {
      height: 100%; }
  .c-funnel-viewport__mainpanel {
    padding: 10px; }
    @media (min-width: 768px) {
      .c-funnel-viewport__mainpanel {
        flex-grow: 1;
        padding: 15px 30px; } }
  .c-funnel-viewport__toggle {
    position: absolute;
    left: 100%;
    top: 100px;
    background-color: #666666;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    cursor: pointer; }
    @media (min-width: 768px) {
      .c-funnel-viewport__toggle {
        display: none; } }
    .c-funnel-viewport__toggle__close, .c-funnel-viewport__toggle__open {
      color: #fff;
      padding: 5px 10px;
      text-decoration: none;
      display: inline-block; }
    .c-funnel-viewport__toggle__close {
      display: none; }
  .c-funnel-viewport.is-opening .c-funnel-viewport__sidepanel {
    transition: all 0.2s ease-out;
    transform: translateX(0); }
  .c-funnel-viewport.is-closing .c-funnel-viewport__sidepanel {
    transition: all 0.2s ease-out; }
  .c-funnel-viewport.is-open .c-funnel-viewport__sidepanel {
    transform: translateX(0); }
  @media (max-width: 768px) {
    .c-funnel-viewport.is-open .c-funnel-viewport__row:after {
      opacity: 1;
      visibility: visible; } }
  .c-funnel-viewport.is-open .c-funnel-viewport__toggle__open {
    display: none; }
  .c-funnel-viewport.is-open .c-funnel-viewport__toggle__close {
    display: inherit; }

.c-funnel-viewport--25p-75p__sidepanel {
  width: 75%; }
