.c-modal--context {
  position: fixed;
  overflow: visible; }
  .c-modal--context__dialog {
    position: absolute;
    transition: transform 0.4s ease-out, left 0.1s ease-out, top 0.1s ease-out; }
  .c-modal--context.c-modal--blueriq {
    overflow: hidden; }
    .c-modal--context.c-modal--blueriq .c-modal__dialog {
      position: relative; }

@media (min-width: 610px) {
  .c-modal--context .c-modal__dialog {
    max-width: 500px; } }

.c-modal--light.c-modal--context {
  background-color: transparent; }
