.c-sidepanel {
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1001;
  background-color: rgba(0, 0, 0, 0.5); }
  .c-sidepanel__mask {
    position: fixed;
    width: 100vw;
    height: 100vh; }
  .c-sidepanel__top {
    flex: 0 0 auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    font-weight: bold;
    -webkit-font-smoothing: antialiased;
    font-size: 24px;
    font-size: 1.5rem;
    line-height: 1.04167;
    color: #ea650d;
    align-items: center;
    text-align: center;
    padding: 17px 21px 17px 33px;
    background-color: #e5ded9; }
  .c-sidepanel__close_btn {
    position: absolute;
    top: 17px;
    left: 21px;
    padding: 6px;
    text-decoration: none;
    cursor: pointer;
    color: #ea650d;
    font-size: 12px;
    font-size: 0.75rem; }
    .c-sidepanel__close_btn:hover {
      color: #999999; }
  .c-sidepanel__wrapper {
    position: absolute;
    display: flex;
    flex-flow: column nowrap;
    transition: right 0.5s ease;
    right: -100vw;
    top: 0;
    bottom: 0;
    z-index: 1;
    width: 100vw;
    background: white;
    overflow: hidden;
    box-sizing: border-box; }
  .c-sidepanel__content {
    flex: 1 0 0;
    overflow: auto;
    padding: 34px 37px; }
  .c-sidepanel__is-open .c-sidepanel__wrapper {
    overflow: auto;
    right: 0; }
  @media (min-width: 768px) {
    .c-sidepanel__wrapper {
      right: -570px;
      width: 570px;
      padding-left: 1px;
      border: 1px solid #666666; } }
