.c-datepicker__icon {
  position: absolute;
  transform: translateY(0.6em) translateX(-1.5em);
  width: 1em;
  height: 1em;
  background: url(../../img/datepicker/c-datepicker__icon.svg) no-repeat center; }

.c-datepicker__dialog {
  min-height: 240px; }
  .c-datepicker__dialog table {
    border-bottom: none;
    margin-bottom: 0; }
    .c-datepicker__dialog table td {
      padding: 0;
      border-top: none; }
  .c-datepicker__dialog__header {
    padding-bottom: 0.5rem; }
    .c-datepicker__dialog__header > .select {
      margin-right: 0.5rem; }

.c-datepicker__select-month {
  width: 8em; }

.c-datepicker__select-year {
  width: 6em; }

.c-datepicker__month {
  width: 100%;
  border: none; }

.c-datepicker__weekday, .c-datepicker__date {
  box-sizing: border-box;
  padding: 0.3rem 0;
  margin: 0.2rem;
  text-align: center; }

.c-datepicker__weekday {
  color: #999999;
  font-size: 0.875rem; }

.c-datepicker__date {
  border-radius: 4px; }
  .c-datepicker__date[data-date] {
    cursor: pointer; }
  .c-datepicker__date--inactive {
    color: #cac7c7; }
  .c-datepicker__date--today {
    font-weight: bold;
    color: #ea650d; }
  .c-datepicker__date--chosen {
    color: white;
    background-color: #ea650d; }
  .c-datepicker__date:hover[data-date] {
    color: inherit;
    background-color: #e5ded9; }

.c-datepicker__weeks {
  border-top: 1px solid #ea650d; }
