input, textarea, select {
  font-family: 'NN Dagny', Arial, Helvetica, Sans-serif;
  font-weight: normal;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.25; }

.monospace {
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 14px;
  font-size: 0.875rem;
  line-height: 1.14286; }

a, a * {
  cursor: pointer; }

a {
  color: #414141; }

p {
  margin: 0 0 15px 0; }

p a,
li a {
  text-decoration: underline; }

address {
  font-style: normal; }

a:hover,
a.active {
  color: #ea650d; }

blockquote {
  font-family: Georgia;
  font-style: italic;
  margin: 0 0 30px 0; }

dl dd.etactive {
  margin: 0; }

strong {
  font-family: 'NN Dagny', Arial, Helvetica, Sans-serif;
  font-weight: bold; }

/* CONTEXT: block shadow
=============================================================================*/
.block--shadow h2, .block--shadow .h2 {
  color: #ea650d; }

/* CONTEXT: responsive block shadow
=============================================================================*/
@media (max-width: 479px) {
  .is-responsive .form-fieldset h2, .is-responsive .form-fieldset .h2 {
    padding: 0; } }

/* THEME: H4 More
=============================================================================*/
h4.more {
  font-size: 0.813em; }

@media (max-width: 479px) {
  .is-responsive .heading-accent-resp {
    color: #ea650d; }
  .is-responsive .block--resp-borderless h2 {
    padding: 0 0 8px 0; } }

.heading--submenu {
  color: #ee7f00;
  font-family: 'NN Dagny', Arial, Helvetica, Sans-serif;
  font-weight: bold;
  margin: 0 0 12px 0;
  font-weight: normal; }

/* PART: Helper classes
=============================================================================*/
.indent1 {
  text-indent: 15px; }

.colored {
  color: #ea650d; }
  .colored .light {
    color: #ee7f00; }
  .colored .medium {
    color: #ea650d; }
  .colored .dark {
    color: #e64415; }

.copy-statement p {
  font-size: 34px;
  font-family: 'NN Dagny', Arial, Helvetica, Sans-serif;
  font-weight: bold; }

.uppercase {
  text-transform: uppercase; }

.footnote {
  color: #999999;
  font-size: 12px;
  font-size: 0.75rem; }

.accent, .accent--big, .accent--small {
  color: #ea650d;
  font-weight: bold; }

.accent--big {
  font-size: 1em; }

.grid--block-iota .accent, .grid--block-iota .accent--big, .grid--block-iota .accent--small {
  font-size: 1.33333em; }

.etfaqanswer p a {
  font-size: 16px;
  font-size: 1rem; }

.client-code {
  letter-spacing: 0.5em; }

.heading--sub {
  margin-top: -15px;
  color: #414141; }

.heading-grey {
  color: #414141; }

.heading-accent {
  color: #ea650d; }
  .heading-accent:hover {
    color: #e64415; }
