.c-modal--datepicker {
  background-color: transparent;
  position: fixed;
  overflow: visible;
  transition-duration: 0s; }

.c-modal--datepicker .c-modal__dialog {
  width: 330px;
  transform: none;
  transition-duration: 1ms; }

.c-modal--datepicker[data-arrow-position] .c-modal__close-btn {
  padding-left: 0;
  margin-left: 0; }
