/* RESPONSIVE TABLE
=============================================================================*/
.data-table--responsive {
  *zoom: 1;
  width: 100%;
  padding: 0;
  margin: 0 0 15px 0; }
  .data-table--responsive:before, .data-table--responsive:after {
    content: " ";
    display: table; }
  .data-table--responsive:after {
    clear: both; }
  .data-table--responsive li {
    width: 50%;
    float: left;
    background: none;
    padding: 0;
    margin: 0; }
    .data-table--responsive li.first {
      width: 100%; }
      .data-table--responsive li.first .cell-title {
        display: block; }
      .data-table--responsive li.first .cell,
      .data-table--responsive li.first .cell-header {
        width: 50%;
        display: block; }
  .data-table--responsive .cell-empty,
  .data-table--responsive .cell-title,
  .data-table--responsive .cell-header,
  .data-table--responsive .cell {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }
  .data-table--responsive .cell-header {
    float: left;
    display: none;
    width: 50%;
    min-height: 24px;
    border-bottom: 2px solid #ea650d;
    font-weight: bold;
    color: #000; }
  .data-table--responsive .cell-header + .cell-header {
    width: 100%;
    display: block; }
  .data-table--responsive .cell-title {
    float: left;
    display: none;
    width: 50%;
    border-bottom: 1px solid #d1d1d1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 5px 5px 0; }
  .data-table--responsive .cell {
    float: left;
    width: 100%;
    border-bottom: 1px solid #d1d1d1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 5px 5px 0; }

