/* COMPONENT: Collapser
=============================================================================*/
.expand {
  position: relative;
  padding-left: 30px;
  text-decoration: none;
  /*collapser trigger with a 'more' styling*/ }
  .expand:before {
    background-color: #ea650d;
    width: 20px;
    height: 20px;
    content: '+';
    text-align: center;
    color: #FFF;
    font-weight: bold;
    position: absolute;
    left: 0;
    top: -3px;
    font-size: 20px;
    font-size: 1.25rem;
    line-height: 1;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    border-radius: 100%; }
  .expand:hover span {
    text-decoration: underline; }
  .expand .close {
    display: none; }
  .expand.more {
    padding-right: 30px;
    padding-left: 0; }
    .expand.more:before {
      display: none; }
    .expand.more:after {
      display: inline-block;
      background-color: #ea650d;
      width: 14px;
      height: 14px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      content: '';
      text-align: center;
      color: #FFF;
      font-weight: bold;
      position: absolute;
      right: 0;
      top: -2px;
      line-height: 12px;
      background: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/control/icons/img/arrow-small-down.svg) no-repeat;
      background-position: 0 0; }
    .expand.more.collapse:after {
      background: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/control/icons/img/arrow-small-up.svg) no-repeat; }

.lt-ie9 .expand.more:after {
  background-image: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/img/ie/arrow-small-down.png);
  background-position: 5px 8px; }

.lt-ie9 .expand.more.collapse:after {
  background-image: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/img/ie/arrow-small-up.png); }

.expand.collapse:before {
  content: '-'; }

.expand.collapse .close {
  display: inherit; }

.expand.collapse .open {
  display: none; }

.collapsible-block {
  margin: 0 0 15px 0; }
  .collapsible-block .more {
    display: inline-block;
    margin: 10px 0 0 0; }

/* CONTEXT: grid--field-row
=============================================================================*/
.grid--field-row.collapsible-block {
  position: relative;
  padding: 0 0 0 0;
  margin: 0; }

/* THEME: Expand link button
=============================================================================*/
.expand--no-label {
  width: 12px;
  padding-left: 12px;
  overflow: hidden; }
  .expand--no-label .close,
  .expand--no-label .open {
    display: none; }

.expand--no-label.collapse .close,
.expand--no-label.collapse .open {
  display: none; }

/* CONTEXT: Expand link button
=============================================================================*/
.grid--field-row.collapsible-block .expand--no-label {
  position: absolute;
  width: 12px;
  top: 5px;
  right: 8px;
  height: 15px;
  z-index: 2;
  padding: 0; }

/* CONTEXT: collapser icon
=============================================================================*/
.collapser-icon {
  display: inline-block;
  height: 14px;
  width: 14px;
  position: relative; }
  .collapser-icon:before {
    content: "";
    position: absolute;
    width: 14px;
    height: 2px;
    background: black;
    top: 6px;
    left: 2px; }
  .collapser-icon:after {
    content: "";
    position: absolute;
    width: 2px;
    height: 14px;
    background: black;
    top: 0;
    left: 8px; }

.collapse .collapser-icon:after {
  display: none; }

