.charactercount {
  margin: 0;
  padding: 0;
  display: inline-block;
  min-width: 100%;
  position: relative;
  line-height: 0.8rem; }

/* PART: Counter text
=============================================================================*/
.charactercount-counter {
  position: relative;
  top: -1px;
  left: 0;
  padding: 4px 6px 3px;
  color: #666666;
  text-align: right;
  display: block;
  border-color: #cac7c7;
  border-radius: 0 0 4px 4px;
  border-style: solid;
  border-width: 0 1px 1px 1px;
  font-size: 11px; }

.charactercount-counter--bottomright {
  position: absolute;
  top: auto;
  right: 0;
  bottom: 0;
  left: auto;
  border-radius: 4px 0 0 0;
  border-width: 1px 0 0 1px; }

.text-field-autosuggest-wrapper {
  margin: 0;
  padding: 0;
  position: relative; }

.text-field-autosuggest-container {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1100;
  background-color: white;
  margin-top: 5px;
  border-width: 0 1px 1px 1px;
  border-color: white;
  border-style: solid;
  display: none;
  border-radius: 0 0 4px 4px; }

.text-field-autosuggest-container--active {
  display: block;
  border-color: #ea650d; }

.text-field-autosuggest-wrapper > .text-field:focus {
  outline-width: 0; }

.text-field:focus + .text-field-autosuggest-container {
  border-color: #ea650d; }

.text-field-autosuggest-optionlist .text-field-autosuggest-option,
.text-field-autosuggest-optionlist .text-field-autosuggest-viewall {
  padding: 4px 9px;
  background-image: none;
  cursor: pointer;
  word-wrap: break-word; }
  .text-field-autosuggest-optionlist .text-field-autosuggest-option:before,
  .text-field-autosuggest-optionlist .text-field-autosuggest-viewall:before {
    content: none; }

.text-field-autosuggest-optionlist .text-field-autosuggest-viewall {
  background-image: none;
  border-top: 1px dotted #cac7c7;
  padding-top: 8px;
  padding-bottom: 0; }
  .text-field-autosuggest-optionlist .text-field-autosuggest-viewall:hover {
    color: #ea650d; }

.text-field-autosuggest-option--focus {
  background-color: #ee7f00; }
  .text-field-autosuggest-option--focus, .text-field-autosuggest-option--focus * {
    color: white; }
