.tabbed-component {
  margin-bottom: 30px; }
  .tabbed-component.accordion .tab-button {
    display: none; }

.tab-header {
  width: 100%;
  border-bottom: 3px solid #e5ded9;
  margin-bottom: 20px; }
  .tabbed-component.accordion:not(.tabbed-component--display-tab-header) .tab-header {
    display: none; }
  .tabbed-component--display-tab-header .tab-header {
    opacity: 0;
    position: absolute; }

.tab-list {
  display: table;
  margin: 0;
  /* PART: Tab links -> opens the corresponding tab panel */ }
  .tab-list .tab {
    padding: 0 0 0 30px;
    display: table-cell;
    box-sizing: border-box;
    margin-bottom: 0; }
    .tab-list .tab:first-child {
      padding-left: 0; }
    .tab-list .tab:before {
      display: none; }
    .tab-list .tab:hover a {
      border-bottom: 3px solid #cac7c7;
      color: #ea650d; }
    .tab-list .tab.active a {
      border-bottom: 3px solid white;
      border-top: 3px solid #ea650d;
      color: #ea650d; }
    .tab-list .tab a {
      font-weight: bold;
      -webkit-font-smoothing: antialiased;
      font-size: 20px;
      font-size: 1.25rem;
      padding: 15px 0;
      margin-bottom: -3px;
      text-decoration: none;
      display: inline-block;
      outline: 0; }

/* PART: Tab panels -> has the content */
.tab.active {
  display: block !important; }

.tab .tabbed-title {
  display: none; }

.tabbed-component--items-2 .tab-list .tab {
  width: 50%; }

.tabbed-component--items-3 .tab-list .tab {
  width: 33.33333%; }

.tabbed-component--items-4 .tab-list .tab {
  width: 25%; }

.tabbed-component--items-5 .tab-list .tab {
  width: 20%; }

.tabbed-component--items-6 .tab-list .tab {
  width: 16.66667%; }

/*
	PART : Tab in accordion mode */
.tabbed-component.accordion {
  border-bottom: 2px solid #cac7c7; }

/*
	PART : Tab Button accordion */
.tab-button {
  display: none; }
  .tab-button a {
    font-weight: bold;
    -webkit-font-smoothing: antialiased;
    font-size: 18px;
    font-size: 1.125rem;
    display: block;
    line-height: 1.5em;
    text-decoration: none;
    padding: 10px 10px 10px 40px;
    border-top: 2px solid #cac7c7;
    position: relative; }
    .tab-button a:after {
      content: "";
      width: 3px;
      height: 15px;
      background: #414141;
      position: absolute;
      left: 10px;
      top: 15px; }
    .tab-button a:before {
      content: "";
      width: 15px;
      height: 3px;
      background: #414141;
      position: absolute;
      left: 4px;
      top: 21px; }
    .tab-button a:hover {
      color: #414141; }
      .no-touchevents .tab-button a:hover,
      .no-touch .tab-button a:hover {
        color: #ea650d; }
        .no-touchevents .tab-button a:hover:after, .no-touchevents .tab-button a:hover:before,
        .no-touch .tab-button a:hover:after,
        .no-touch .tab-button a:hover:before {
          background: #ea650d; }
  .tabbed-component.accordion .tab-button {
    display: block; }
  .tab-button.active {
    border-color: transparent; }
    .tab-button.active a {
      border-top: 2px solid #ea650d;
      color: #ea650d; }
      .tab-button.active a:before {
        background-color: #ea650d; }
      .tab-button.active a:after {
        display: none; }

.wmeditview .tabbed-component > div.tab {
  display: none !important; }

.wmeditview .tabbed-component > div.tab.active {
  display: block !important; }

/* THEME: Advisor
=============================================================================*/
.tabbed-component--advisor .tab-header {
  border-bottom-width: 2px; }

.tabbed-component--advisor .tab-list .tab {
  padding-left: 0;
  overflow: hidden; }
  .tabbed-component--advisor .tab-list .tab a {
    padding-right: 15px;
    padding-left: 15px;
    border-style: solid;
    border-width: 2px 2px 0 2px;
    border-color: transparent;
    background-color: #f1edeb;
    border-radius: 8px 8px 0 0;
    margin-right: 2px;
    margin-bottom: -2px; }
  .tabbed-component--advisor .tab-list .tab.active {
    overflow: visible; }
    .tabbed-component--advisor .tab-list .tab.active a {
      background-color: white;
      border-color: #e5ded9;
      border-right-width: 2px;
      border-left-width: 2px;
      border-radius: 8px 8px 0 0; }
  .tabbed-component--advisor .tab-list .tab:hover a {
    border-bottom: 0; }

.tabbed-component--advisor .tab.active {
  display: block; }
