/* Notification
=============================================================================*/
.notification--success, .notification--warning, .notification--info, .notification--error, .notification--type2-success, .notification--type2-warning, .notification--type2-info, .notification--type2-error {
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  position: relative;
  padding: 10px;
  margin-bottom: 20px; }
  .notification--success:before, .notification--warning:before, .notification--info:before, .notification--error:before, .notification--type2-success:before, .notification--type2-warning:before, .notification--type2-info:before, .notification--type2-error:before {
    content: "";
    display: block;
    position: absolute;
    top: 20px;
    left: 25px;
    width: 58px;
    height: 58px;
    background-image: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/control/notification/img/icons.svg);
    background-repeat: no-repeat; }

/* PART: Title
=============================================================================*/
.notification-title {
  font-size: 24px;
  font-size: 1.5rem;
  margin: 10px 0 0 100px;
  color: white; }

/* PART: Content
=============================================================================*/
.notification-content {
  *zoom: 1;
  margin: 0 0 0 100px; }
  .notification-content:before, .notification-content:after {
    content: " ";
    display: table; }
  .notification-content:after {
    clear: both; }

/* PART: Text
=============================================================================*/
.notification-text {
  font-size: 16px;
  font-size: 1rem;
  color: white;
  padding-right: 1em; }

/* THEME: Success message
=============================================================================*/
.notification--success {
  background-color: #96b439; }
  .notification--success:before {
    background-position: 0 -261px; }

/* THEME: Warning message
=============================================================================*/
.notification--warning {
  background-color: #ea650d; }
  .notification--warning:before {
    background-position: 0 -174px; }

/* THEME: Info message
=============================================================================*/
.notification--info {
  background-color: #7caad6; }
  .notification--info:before {
    background-position: 0 -87px; }

/* THEME: Error message
=============================================================================*/
.notification--error {
  background-color: #d70000; }
  .notification--error:before {
    background-position: 0 0; }

/* THEME: Type 2 notifications
=============================================================================*/
[class^='notification--type2-'] {
  background-color: white; }
  [class^='notification--type2-'] .notification-title,
  [class^='notification--type2-'] .notification-text {
    color: #414141; }

/* THEME: Success message
=============================================================================*/
.notification--type2-success {
  border: solid 1px #96b439; }
  .notification--type2-success:before {
    background-position: -80px -261px; }

/* THEME: Warning message
=============================================================================*/
.notification--type2-warning {
  border: solid 1px #ea650d; }
  .notification--type2-warning:before {
    background-position: -80px -174px; }

/* THEME: Info message
=============================================================================*/
.notification--type2-info {
  border: solid 1px #7caad6; }
  .notification--type2-info:before {
    background-position: -80px -87px; }

/* THEME: Error message
=============================================================================*/
.notification--type2-error {
  border: solid 1px #d70000; }
  .notification--type2-error:before {
    background-position: -80px 0; }

/* PART: Close button
=============================================================================*/
.notification-close-button {
  background: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/control/notification/img/close-button.png) no-repeat center;
  width: 10px;
  height: 10px;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px; }

/* THEME: Smaller icons on mobile
=============================================================================*/
/*@media (min-width: $tablet_break_point) {*/
@media (max-width: 479px) {
  [class^='notification--']:before {
    top: 24px;
    left: 12px;
    width: 30px;
    height: 30px;
    background-size: 69px; }
  .notification-title,
  .notification-content {
    margin-left: 40px; }
  .notification--success:before {
    background-position: 0 -130px; }
  .notification--warning:before {
    background-position: 0 -87px; }
  .notification--info:before {
    background-position: 0 -43px; }
  .notification--error:before {
    background-position: 0 0; }
  .notification--type2-success:before {
    background-position: -40px -130px; }
  .notification--type2-warning:before {
    background-position: -40px -87px; }
  .notification--type2-info:before {
    background-position: -40px -43px; }
  .notification--type2-error:before {
    background-position: -40px 0; } }

/* CONTROL: Spinner
=============================================================================*/
.spinner {
  margin: 0 0 20px 0;
  padding: 9px 9px 9px 82px;
  display: inline-block;
  min-height: 60px;
  background-color: #fff;
  background-position: 6px center;
  background-repeat: no-repeat;
  background-image: url(../../img/_/_/nn-psdl-legacy/src/nn-responsive/img/spinner.gif); }

.spinner-content {
  margin: 0;
  display: inline-block; }

.spinner--overlay {
  position: fixed;
  z-index: 1050;
  left: 50%;
  top: 50%;
  min-width: 400px;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }
